import React, { Fragment } from 'react'
import PropTypes from 'prop-types'
import { t } from 'i18next'
import * as R from 'ramda'
import CandidatesViewGridConnector from 'Employer/connectors/Views/Candidates/CandidatesViewGridConnector'
import CandidatesBulkManagerConnector from 'Employer/connectors/BulkManager/CandidatesBulkManagerConnector'
import { SearchResults } from 'Algolia/components/SearchResult'
import CandidatesStarRatingWrapper from 'Employer/components/CandidatesStarRatingWrapper'
import { JobsViewLoader } from 'Shared/components/Main/MainLoaders'
import { searchStateToUrl, urlToSearchState } from 'Shared/helpers/querySearch'
import { MIN_CANDIDATES_RATING_VALUE } from 'Shared/constants/Candidates'
import { isCandidatesDefaultSearch } from 'Employer/utils/isCandidatesDefaultSearch'
import { getCandidateSearchCriteria } from 'Employer/utils/getCandidateSearchCriteria'

import './CandidatesView.scss'
import BuyCreditsBanner from 'Employer/connectors/BuyCreditsBanner'
import { Helmet } from 'react-helmet'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import { candidateViewedFiltersNames, candidateContactedFiltersNames } from 'Shared/constants/candidatesFilters'
import { handleCandidateIdFilters } from 'Shared/helpers/Algolia'
import CandidatesSearchBoxConnector from 'Employer/connectors/Components/CandidatesSearchBoxConnector'

class CandidatesView extends React.PureComponent {
  state = {
    seeMoreCandidatesModalVisible: false,
    isDropdownOpen: false,
    isSavedSearchLoaded: false,
    viewedFilterValue: candidateViewedFiltersNames().all,
    contactedFilterValue: candidateContactedFiltersNames().all
  }

  async componentDidMount () {
    await this.props.fetchCountries()
    await this.props.fetchLanguages()

    await this.props.fetchCurrentLocationIfNeeded()
    const candidateCountries = R.pathOr([], ['currentLocation', 'candidateCountries'], this.props)
    const currentSearch = urlToSearchState(this.props.location.search)
    candidateCountries.length &&
    (R.isNil(currentSearch.refinementList) || R.isEmpty(currentSearch.refinementList) || !R.path(['refinementList', 'countryOfResidence'], currentSearch).length) &&
    this.props.history.replace({
      pathname: this.props.history.location.pathname,
      search: searchStateToUrl({
        ...currentSearch,
        refinementList: {
          countryOfResidence: candidateCountries.map(key => R.path(['countries', key, 'country'], this.props))
        }
      })
    })
  }

  hasQueryChanged (nextProps) {
    const currentSelectedSearchQuery = R.pipe(
      R.propOr(false, 'query'),
      JSON.parse
    )(nextProps.selectedSearch)
    const currentUrlSearchQuery = urlToSearchState(nextProps.location.search, true)

    return R.not(R.equals(currentSelectedSearchQuery, {...currentUrlSearchQuery, configure: {...R.dissoc('optionalFilters', currentUrlSearchQuery.configure)}}))
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.selectedSearch.id && this.hasQueryChanged(nextProps)) {
      this.props.history.push({
        pathname: '/employer/candidates',
        search: nextProps.location.search
      })
    }

    if (!nextProps.match.params.savedSearchId || nextProps.match.params.savedSearchId !== this.props.match.params.savedSearchId) {
      this.setState({ isSavedSearchLoaded: false })
    }

    if (R.not(R.isEmpty(nextProps.searchStateValues)) && !isCandidatesDefaultSearch(nextProps.searchStateValues) && !nextProps.match.params.savedSearchId) {
      const fireEvent = payload => this.props.manualSegmentScripts.searchForCandidates(this.props.employerId, payload, this.props.userEmail)

      R.pipe(
        getCandidateSearchCriteria,
        fireEvent
      )(nextProps.searchState)
    }
  }

  componentDidUpdate (prevProps) {
    if (this.props.match.params.savedSearchId && this.props.match.params.savedSearchId !== prevProps.match.params.savedSearchId) {
      this.setState({ isSavedSearchLoaded: true }, () => setTimeout(() => this.setState({ isSavedSearchLoaded: false }), 1500))
    }

    if (this.props.viewedFilterValue !== prevProps.viewedFilterValue || this.props.contactedFilterValue !== prevProps.contactedFilterValue) {
      this.handleFilterHitsByIds()
    }
  }

  isFilterLoaded = filterName => this.state.isSavedSearchLoaded && R.has(filterName)(this.props.searchStateValues)

  handleFilterHitsByIds = () => {
    const { viewedFilterValue, contactedFilterValue, viewedCandidatesIds, contactedCandidatesIds, addObjectIdFilters } = this.props
    handleCandidateIdFilters(viewedFilterValue, contactedFilterValue, viewedCandidatesIds, contactedCandidatesIds, addObjectIdFilters)
  }

  generateResultLabel = (hitsLength) => {
    const showHitsLength = isCandidatesDefaultSearch(this.props.searchStateValues) &&
      this.state.viewedFilterValue === candidateViewedFiltersNames().all &&
      this.state.contactedFilterValue === candidateContactedFiltersNames().all
      ? ''
      : `(${hitsLength})`

    return (
      this.props.hasActiveMembership ? `${t('common.searchResults')} ${showHitsLength}` : `${t('common.searchResults')}`
    )
  }

  isRatingLoaded = () => {
    return this.isFilterLoaded('rating') && R.path(['rating', 'min'], this.props.searchStateValues) > 0
  }

  setSearchResults = hitsNumber => this.setState({ hitsNumber })

  showStarRatingFilterIfNeeded = () => !this.props.isAutoApprovalEnabled && (
    <CandidatesStarRatingWrapper
      min={0}
      max={5}
      shouldUpdate={R.isEmpty(this.props.searchState)}
      attribute='rating'
      isLoaded={this.isRatingLoaded()}
      ratingValue={parseInt(R.pathOr(
        MIN_CANDIDATES_RATING_VALUE,
        ['props', 'searchState', 'range', 'rating', 'min'],
        this
      ), 10)}
    />
  )

  render () {
    const { canUseDatabaseSearch, selectedSearch, countries, languages } = this.props
    return (
      <Fragment>
        <Helmet>
          <title>{t('meta.employer.title.searchProfiles')}</title>
          <meta property='og:title' content={t('meta.employer.title.searchProfiles')} />
        </Helmet>
        <PageTopHeaderMobile title={t('common.candidates')} />
        {!R.isEmpty(languages) && !R.isEmpty(countries)
          ? (
            <Fragment>
              <CandidatesSearchBoxConnector />
              <div className='container container--large candidates-view parsing--hidden'>
                {!canUseDatabaseSearch && <BuyCreditsBanner />}
                <div className='columns candidates-view-container__search-wrapper'>
                  <div className='column col-6 col-sm-12'>
                    <SearchResults
                      setSearchResults={this.setSearchResults}
                      generateLabel={this.generateResultLabel}
                      selectedSearchName={selectedSearch.name}
                      hideCurrentRefinements
                    />
                  </div>
                  <div className='column col-6 col-sm-12 candidates-view__additional-filters'>
                    {this.showStarRatingFilterIfNeeded()}
                  </div>
                </div>
                <CandidatesViewGridConnector
                  hitsNumber={this.state.hitsNumber}
                  viewedFilterValue={this.state.viewedFilterValue}
                />
                <CandidatesBulkManagerConnector />
              </div>
            </Fragment>
          )
          : <JobsViewLoader />
        }
      </Fragment>
    )
  }
}

CandidatesView.propTypes = {
  addFilter: PropTypes.func,
  removeFilter: PropTypes.func,
  fetchLanguages: PropTypes.func,
  fetchCountries: PropTypes.func,
  countries: PropTypes.object,
  languages: PropTypes.object,
  hasActiveMembership: PropTypes.bool,
  selectedSearch: PropTypes.object,
  searchStateValues: PropTypes.object.isRequired,
  addObjectIdFilters: PropTypes.func
}

export default CandidatesView



